class Cooldown {
    status = "cold";
    timer;
    warm = () => {
        clearTimeout(this.timer);
        this.status = "warm";
    };
    cool = () => {
        this.status = "cooling";
        const currentTimer = setTimeout(() => {
            this.status = "cold";
            if (currentTimer === this.timer)
                this.timer = undefined;
        }, 500);
        this.timer = currentTimer;
    };
}
export default new Cooldown();
